.form {
    overflow: hidden;
    background: rgba(250, 250, 250, 1); 
    margin: 70px 0 0;

    @include vp-767 {
        margin: 50px 0 0;
    }

    &__wrapper {
        padding: 86px 0 100px;

        @include vp-767 {
            padding: 30px 0 70px;
        }
    }

    .container-title {
        display: flex;
        -webkit-flex-direction: column;
        -moz-flex-direction: column;
        -ms-flex-direction: column;
        -o-flex-direction: column;
        flex-direction: column;
        min-height: 100px;
        position: relative;
        margin-bottom: 20px;
    }

    .container-title span {
        position: absolute;
        top: 0;
        left: -55px;
        height: 100px;
        line-height: 100px;
        z-index: 1;
        font-size: 64px;
        font-weight: 700;
        color: #1A1919;
        opacity: .035;
        text-transform: uppercase;

        @media screen and (max-width: 1360px) {
            left: 25px;
            font-size: 50px;
        }

        @include vp-767 {
            font-size: 40px;
            left: 0;
        }
    }

    .container-small {
        font-size: 30px;
        line-height: 38px;
        letter-spacing: 1px;
        font-weight: 700;
        text-transform: uppercase;
        margin-top: 58px;

        @include vp-1023 {
            font-size: 20px;
            line-height: 30px;
        }

        @include vp-767 {
            font-size: 20px;
            margin-top: 50px;
            line-height: 30px;
        }
    }

    &__text {
        max-width: 520px;
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 16px;
        line-height: 145%;
        letter-spacing: 0%;
        color: rgba(26, 25, 25, 1);
        margin: 0 0 30px;

        a {
            color: rgba(131, 117, 78, 1);
            font-family: Gotham Pro;
            font-weight: 700;
            font-size: 16px;
            line-height: 145%;
            letter-spacing: 0%;
            transition: .3s;
            @media screen and (max-width: 800px) {
                display: inline;
                white-space: nowrap;
                font-size: 14px;
            }

        }

        a:hover {
            opacity: 0.8;
        }

        @media screen and (max-width: 800px) {
            max-width: 100%;
            font-size: 14px;
        }
    }

    &__content {
        display: grid;
        grid-template-columns: 1.1fr 1fr;
        gap: 75px;

        @include vp-1023 {
            gap: 30px;
        }

        
        @media screen and (max-width: 800px) {
            grid-template-columns: 1fr;
            gap: 0;
        }
    }

    &__top {
        display: flex;
        flex-direction: column;
        gap: 16px;
        margin-bottom: 30px;
    }

    .custom-input input {
        color:rgba(26, 25, 25, 1);
        background: transparent;
        padding: 0;
        border-bottom: 1px solid black;
        border-radius: 0;
    }

    .custom-input .custom-input__label {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        letter-spacing: 0%;
        color: rgba(26, 25, 25, 0.35);
        margin-bottom: 40px;

        @media screen and (max-width: 500px) {
            margin-bottom: 30px;
        }
    }

    .form-checkbox {
        display: flex;
        align-items: flex-start; 
        width: 100%; 
        margin-bottom: 40px;
      
        &__label {
          display: flex;
          align-items: flex-start;
          cursor: pointer;
          width: 100%;
        }
      
        &__input {
          position: absolute;
          opacity: 0;
          width: 0;
          height: 0;
      
          &:checked + .form-checkbox__custom {
            background-color: #2a5885;
            border-color: #2a5885;
      
            &::after {
              content: '';
              display: block;
            }
          }
        }
      
        &__custom {
          position: relative;
          flex-shrink: 0;
          width: 18px;
          height: 18px;
          border: 2px solid #ccc;
          border-radius: 4px;
          margin-right: 12px;
          margin-top: 2px; 
      
          &::after {
            content: '';
            position: absolute;
            display: none;
            left: 5px;
            top: 1px;
            width: 5px;
            height: 10px;
            border: solid white;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
          }
        }
      
        &__text {
          flex-grow: 1; 
          font-size: 14px;
          line-height: 1.4;
          text-align: left;
          color: #333;
      
          a {
            color: #2a5885;
            text-decoration: underline;
            display: inline;
            &:hover {
              text-decoration: none;
            }
          }
        }
    }

    &__btn {
        max-width: 280px;
        width: 100%;
        background: rgba(131, 117, 78, 1);
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        leading-trim: Cap height;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 1);
        padding: 24px;
        transition: .3s;

        @media screen and (max-width: 800px) {
            margin: 0 auto;
            display: block;
        }

        
        @media screen and (max-width: 500px) {
            max-width: 100%;
            background: rgba(26, 25, 25, 1);
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__image {
        width: 520px;
        height: 100%;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-1023 {
            width: 450px;
        }

        @media screen and (max-width: 800px) {
            display: none;
        }
    }

}