.wrapper-top {
    position: relative;
    overflow: hidden;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100vw;
        height: 100%;
        z-index: 1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-767 {
            width: 150%;
            left: -50%;
            // transform: translateX(-10%);
        }

        @media screen and (max-width: 500px) {
            width: 200%;
            left: -100%;
            // transform: translateX(-10%);
        }

        @include vp-389 {
            width: 250%;
            left: -150%;
            // transform: translateX(-10%);
        }
    }
}