.mark {
    display: none;

    @media screen and (max-width: 500px) {
        display: block;
    }

    &__wrapper {
        padding: 40px 0 0;
    }

    &__content {
        margin-bottom: 50px;
    }

    &__image {
        width: 100px;
        height: 100%;
        margin-bottom: 10px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__title {
        font-family: Gotham Pro;
        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
        letter-spacing: 1px;
        vertical-align: middle;
        text-transform: uppercase;
        margin: 0 0 20px;
    }

    &__stars {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        margin-bottom: 45px;
        svg {
            width: 48;
            height: 48px;
            flex-shrink: 0;
        }
    }

    &__link {
        max-width: 100%;
        background: rgba(131, 117, 78, 1);
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        leading-trim: Cap height;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 1);
        padding: 24px;
    }

    &__map {
        position: relative;
        width: 100%;
        
        filter: grayscale(100%);
        iframe {
            width: 100%;
            height: 440px;
        }
    }

    &__box {
        position: absolute;
        top: 250px;
        left: 20px;
        background: rgba(255, 255, 255, 1);
        max-width: 250px;
        padding: 10px;

        span {
            font-family: Gotham Pro;
            font-weight: 400;
            font-size: 10px;
            line-height: 145%;
            letter-spacing: 0%;
            display: block;
            margin-bottom: 5px;
        }

        h2 {
            font-family: Gotham Pro;
            font-weight: 700;
            font-size: 14px;
            line-height: 145%;
            letter-spacing: 0px;
            text-transform: uppercase;
            margin: 0 0 10px;
        }

        p {
            font-family: Gotham Pro;
            font-weight: 400;
            font-size: 12px;
            line-height: 145%;
            letter-spacing: 0%;
            margin: 0 0 10px;
        }

        a {
            font-family: Gotham Pro;
            font-weight: 400;
            font-size: 12px;
            line-height: 145%;
            letter-spacing: 0%;
            text-decoration: underline;
            text-decoration-style: solid;
            text-decoration-offset: 0%;
            text-decoration-thickness: 0%;
            text-decoration-skip-ink: auto;

        }
    }

}