.reviews {
    overflow: hidden;

    &__wrapper {
        padding: 50px 0;

        @include vp-767 {
            padding: 30px 0;
        }
    }

    &__buttons {
        position: static;
        display: none;
        flex-direction: row-reverse;
        max-width: 120px;
        width: 100%;
        justify-content: center;
        align-items: center;
        gap: 10px;
        align-self: flex-end;

        @include vp-767 {
            display: flex;
        }
        
    }

    .swiper-button-prev,
    .swiper-button-next {
        position: static;
        margin: 0;
        flex-shrink: 0;
        &::after {
            display: none;
        }
    
        width: 50px;
        height: 50px;
        transition: .3s;
        svg {
          transition: .3s;
        }

        &:hover {
           opacity: 0.7;
        }

        @include vp-767 {
            width: 40px;
            height: 40px;
        }
    }
    
    .swiper-button-next {
        svg {
            transform: rotate(-180deg);
        }
    }

    &__slider.swiper {
        width: 100%;
        height: 100%;
        overflow-x: visible;
        display: flex;

        @include vp-767 {
            margin-bottom: 30px;
        }
    }

    .swiper-wrapper {
        height: auto;
    }

    .swiper-slide {
        width: auto;
        display: flex;

        @media screen and (max-width: 500px) {
            width: 320px;
        }

        @include vp-389 {
            width: auto;
        }
    }

    &__content {
        padding: 20px;
        display: flex;
        flex-direction: column;
        height: 100%;
        background: rgba(250, 250, 250, 1);
    }

    &__top {
        display: flex;
        gap: 16px;
        
    }

    &__ava {
        width: 80px;
        height: 80px;
        flex-shrink: 0;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-767 {
            width: 70px;
            height: 70px;
            
        }
    }

    &__top {
        margin-bottom: 20px;
    }

    &__name {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 18px;
        line-height: 145%;
        letter-spacing: 1.6px;
        text-transform: uppercase;
        color: rgba(26, 25, 25, 1);
        margin: 0 0 10px;

        @include vp-767 {
            font-size: 16px;
        }
    }

    &__who,
    &__path {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        line-height: 145%;
        letter-spacing: 0%;
        color: rgba(26, 25, 25, 1);
        margin: 0 0 5px;
        span {
            color: gray;
        }

        @media screen and (max-width: 500px) {
            font-size: 10px;
        }
    }

    &__path {
        margin: 0;
    }

    &__text {
        max-width: 355px;
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 16px;
        line-height: 145%;
        letter-spacing: 0%;
        color: rgba(26, 25, 25, 1);
        margin: 0 0 10px;

        @include vp-767 {
            font-size: 15px;
        }

        @include vp-389 {
            font-size: 13px;
        }
    }

    &__link {
        width: max-content;
        font-family: Gotham Pro;
        font-weight: 450;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 1px;
        vertical-align: middle;
        text-transform: uppercase;
        color: rgba(26, 25, 25, 1);
        border-bottom: 1px solid rgba(26, 25, 25, 1);
        padding-bottom: 5px;
        margin-top: auto;
        transition: .3s;
    }

    &__link:hover {
        opacity: 0.7;
    }

    &__video-preview {
        width: 290px;
        height: 100%;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.4);
            z-index: 1; 
        }

        // @media screen and (max-width: 500px) {
        //     width: 280px;
        //     height: 100%;
        // }

    }

    .video-play-btn {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: transparent;
        border: none;
        padding: 0;
        cursor: pointer;
        transition: transform 0.3s;
        z-index: 2;
        &:hover {
          transform: translate(-50%, -50%) scale(1.1);
          
          .play-icon circle {
            stroke: #ff0000; // Изменение цвета при наведении
          }
        }
        
        &:focus {
          outline: none;
        }

        svg {
            width: 50px;
            height: 50px;

            @include vp-389 {
                width: 40px;
                height: 40px;
            }
        }
    }

    &__image {
        width: 320px;
        height: 100%;
        position: relative;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; 
        }

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.2);
            z-index: 1; 
        }

        
        // @media screen and (max-width: 500px) {
        //     width: 280px;
        //     height: 100%;
        // }
    }
}