.mobile {
    display: none;
    @media screen and (max-width: 500px) {
        display: block;
    }

    &__wrapper {
        padding:  50px 0;
    }

    &__title {
        font-family: Gotham Pro;
        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
        letter-spacing: 1px;
        vertical-align: middle;
        text-transform: uppercase;
        margin: 0 0 30px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-bottom: 50px;
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 10px;
        height: 100%;
    }

    &__image {
        width: 125px;
        height: 149px;
        flex-shrink: 0;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-389 {
            width: 95px;
            height: 110px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        
        span {
            font-family: Gotham Pro;
            font-weight: 400;
            font-size: 10px;
            line-height: 145%;
            letter-spacing: 0%;
            display: block;
            margin-bottom: 5px;
        }
    }

    &__name {
        font-family: Gotham Pro;
        font-weight: 700;
        font-size: 16px;
        line-height: 145%;
        letter-spacing: 1.6px;
        text-transform: uppercase;
        margin: 0 0 10px;

        span {
            font-size: 16px;
            line-height: 145%;
            letter-spacing: 1.6px;
            text-transform: uppercase;
            font-weight: 500;

            @include vp-389 {
                font-size: 14px;
            }
        }

        @include vp-389 {
            font-size: 14px;
        }
    }

    &__link {
        margin-top: auto;
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 14px;
        line-height: 145%;
        letter-spacing: 0%;
        text-decoration: underline;
        text-decoration-style: solid;
        text-decoration-offset: 0%;
        text-decoration-thickness: 0%;
        text-decoration-skip-ink: auto;
        color: rgba(131, 117, 78, 1);
        transition: .3s;
    }

    &__link:hover {
        opacity: 0.8;
    }



    #calendar {
        
        font-family: Arial, sans-serif;
        user-select: none;
        margin-bottom: 50px;
    }
      
    .calendar-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
        gap: 20px;
    }
      
    .calendar-grid {
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: 5px;
    }

    .month-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
      
    .day-name {
        font-weight: bold;
        text-align: center;
        padding: 5px 0;
    }
      
    .day {
        text-align: center;
        padding: 10px 0;
        cursor: pointer;
        border-radius: 4px;
    }
      
    .day:hover {
        background: #e0e0e0;
    }
      
    .day.disabled {
        color: #ccc;
        cursor: default;
    }
      
    .day.selected {
        background-color: #007bff;
        color: white;
    }

    #selected-date {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 14px;
        line-height: 145%;
        letter-spacing: 0%;
        text-decoration: underline;
        text-decoration-style: solid;
        text-decoration-offset: 0%;
        text-decoration-thickness: 0%;
        text-decoration-skip-ink: auto;
        color: rgba(131, 117, 78, 1);
    }
    

    &__prog {
        display: flex;
        flex-direction: column;
    }

    &__prog-info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
    }

    &__prog-text {
        font-family: Gotham Pro;
        font-weight: 700;
        font-size: 14px;
        line-height: 145%;
        letter-spacing: 0px;
        text-transform: uppercase;
        margin: 0;
    }

    &__prog-middle {
        display: flex;
        flex-direction: column;

        span {
            font-family: Gotham Pro;
            font-weight: 450;
            font-size: 14px;
            leading-trim: Cap height;
            line-height: 120%;
            letter-spacing: 0%;
            vertical-align: middle;
            text-transform: uppercase;
            margin-bottom: 10px;
            display: block;
        }
    } 

    &__prog-middle-text {
        display: flex;
        flex-direction: row-reverse;
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 10px;
        leading-trim: Cap height;
        line-height: 120%;
        letter-spacing: 0%;
        vertical-align: middle;
        margin: 0 0 50px;
        align-items: start;
        justify-content: start;
        gap: 6px;

        svg {
            width: 12px;
            height: 12px;
            flex-shrink: 0;
        }
    }

    &__prog-link {
        max-width: 100%;
        background-color: rgba(131, 117, 78, 1);
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        leading-trim: Cap height;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        text-transform: uppercase;
        padding: 24px;
        color: rgba(255, 255, 255, 1);
        transition: .3s;
        @include vp-389 {
            font-size: 11px;
        }
    }
}

.accordion {
    max-width: 600px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    height: auto;
    margin-bottom: 50px;
  }
  
  .accordion-item {
    border-bottom: 1px solid #ccc;
  }
  
  .accordion-header {
    cursor: pointer;
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
    gap: 15px;
    align-items: center;
    user-select: none;
    font-family: Gotham Pro;
    font-weight: 450;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 0%;
    vertical-align: middle;

    transition: background-color 0.3s ease;

    svg {
        transform: rotate(-180deg);
        transition: .3s;
    }

    @include vp-389 {
        font-size: 18px;
    }
  }
  
  .accordion-header.active {
    svg {
        transform: rotate(0deg);
    }
  }
  .accordion-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease;
    padding: 0 20px;
  }
  
  .accordion-content.open {
    padding: 15px 0px;
    max-height: fit-content; /* достаточно большое значение для плавного раскрытия */
  }

  .video-preview {
    width: auto;
    height: 550px;
    position: relative;
    margin-bottom: 20px;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}


.video-play-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
    transition: transform 0.3s;
    z-index: 2;
    &:hover {
      transform: translate(-50%, -50%) scale(1.1);
      
      .play-icon circle {
        stroke: #ff0000; // Изменение цвета при наведении
      }
    }
    
    &:focus {
      outline: none;
    }

    svg {
        width: 50px;
        height: 50px;

        @include vp-389 {
            width: 40px;
            height: 40px;
        }
    }
}

.accord-text {
    font-family: Gotham Pro;
    font-weight: 500;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0%;
    margin: 0 0 30px;
}

.accord-list {
    list-style: disc;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 30px;
    padding-left: 20px;
}

.accord-list li {
    list-style: disc;
}

.links {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;

    a {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 14px;
        line-height: 145%;
        letter-spacing: 0%;
        text-decoration: underline;
        text-decoration-style: solid;
        text-decoration-offset: 0%;
        text-decoration-thickness: 0%;
        text-decoration-skip-ink: auto;
        color: rgba(131, 117, 78, 1);
    }

   
}