

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 3;
  background: rgba(16, 16, 29, 0.9);
  

  &__wrapper {
    padding: 33px 0 31px;
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
  }


 
  &__logo {
   position: relative;
   z-index: 11;
  }

  &__logo-link {
    font-family: Raleway;
    font-weight: 800;
    font-size: 28px;
    line-height: 100%;
    letter-spacing: 0%;
    vertical-align: middle;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 1);
  }

  &__logo-link span {
    color: #42A9ED;
  }

  &__menu {
    display: flex;
    align-items: center;
    justify-content: end;
    gap: 60px;
    flex-wrap: wrap;
    @media screen and (max-width: 900px) {
      gap: 30px;
    }
  }
  


  &__link {
    font-family: Lato;
    font-weight: 600;
    font-size: 13px;
    line-height: 100%;
    letter-spacing: 0%;
    text-align: right;
    vertical-align: middle;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 1);
    transition: .3s;

    &:hover {
      opacity: 0.6;
    }

    &--active {
      color: rgba(67, 171, 240, 1);
    }
  }


  &.is-open .main-nav__toggle {
    background: transparent;
    border-radius: 2px;
  }
  
  &.is-open .main-nav {
    position: relative;
  }
  
  &.is-open .main-nav__toggle span:nth-child(1) {
    top: 17px;
    -webkit-transform: rotate(-47deg) translate(-5px, 11px);
    -ms-transform: rotate(-45deg) translate(0, -50%);
    transform: rotate(-45deg) translate(0px, 5px);
    background: rgb(255, 255, 255);
    height: 4px;
    width: 40px;
  }
  
  &.is-open .main-nav__toggle span:nth-child(2) {
    opacity: 0;
    visibility: hidden;
  }
  
  &.is-open .main-nav__toggle span:nth-child(3) {
    top: 25px;
    -webkit-transform: rotate(45deg) translate(0, 50%);
    -ms-transform: rotate(45deg) translate(0, 50%);
    transform: rotate(45deg) translate(0px, -7px);
    background: rgb(255, 255, 255);
    height: 4px;
    width: 40px;
  }
  
  &.is-open .header__menu {
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
  
  .main-nav__toggle {
    position: relative;
    display: none;
    width: 50px;
    height: 40px;
    padding: 0;
    z-index: 11;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: opacity rgb(255, 255, 255);
    -o-transition: opacity rgb(255, 255, 255);
    transition: opacity rgb(255, 255, 255);
  }
  
  .main-nav__toggle span {
    display: block;
    position: absolute;
    left: 10px;
    height: 4px;
    width: 40px;
    border-radius: 2px;
    background-color: rgb(255, 255, 255);
    -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  }
  
  .main-nav__toggle span:nth-child(1) {
    top: 8px;
  }
  
  .main-nav__toggle span:nth-child(2) {
    top: 50%;
  
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  .main-nav__toggle span:nth-child(3) {
    bottom: 8px;
  }
  
  .main-nav__toggle.focus-visible:focus {
    opacity: 0.8;
  }
  


  @media (max-width: 768px) {

    .main-nav__toggle {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
    }

    &__menu {
      z-index: 10;
      position: fixed;
      -webkit-box-pack: start;
      -ms-flex-pack: start;
      justify-content: flex-start;
      flex-direction: column;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
      overflow: auto;
      top: 0px;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 130px 20px 50px;
      background: rgba(16, 16, 29, 0.9);
      -webkit-transition: visibility 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
      -o-transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease;
      transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease;
      transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
      -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
      transform: translateX(100%);
    }

  

  }

  @include vp-389 {
    .main-nav__toggle {
      width: 40px;
    }


    .main-nav__toggle span {
      width: 30px;
    }


    &.is-open .main-nav__toggle span {
      width: 30px !important;
    }

    &__logo-link {
      font-size: 22px;
    }
  }

}