.intro {
    position: relative;
    overflow: hidden;
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
      
        
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(216.73deg, rgba(0, 0, 0, 0) 10.59%, rgba(0, 0, 0, 0.5) 67.54%),
                      linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
          z-index: 2;
          mix-blend-mode: multiply; 
        }
      
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          filter: grayscale(100%); 
          position: relative;
          z-index: 1;
        }
    }


    &__wrapper {
        
        z-index: 2;
        padding: 30px 0 70px;
        display: flex;
        flex-direction: column;

        @include vp-767 {
            padding: 50px 0 45px;
        }
    }

    &__top {
        order: 2;

        @include vp-767 {
            order: 1;
            margin-bottom: 295px;
        }
    }

    &__info {
        order: 1;
        display: flex;
        gap: 16px;
        margin-bottom: 320px;

        @include vp-767 {
            order: 2;
            margin-bottom: 16px;
            flex-direction: column;
        }
    }

    &__title {
        font-family: Gotham Pro;
        font-weight: 700;
        font-size: 38px;
        line-height: 120%;
        letter-spacing: 1px;
        vertical-align: middle;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 12px;

        @include vp-767 {
            font-size: 30px;
        }
    }

    &__text {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        letter-spacing: 0%;
        margin: 0 0 40px;
        color: rgba(255, 255, 255, 1);

        @include vp-767 {
            font-size: 14px;
        }

        @media screen and (max-width: 520px) {
            font-size: 12px;
        }
    }

    
    &__btn {
        order: 3;
        box-sizing: border-box;
        max-width: 336px;
        width: 100%;
        background: rgba(255, 255, 255, 1);
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        leading-trim: Cap height;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        vertical-align: middle;
        text-transform: uppercase;
        color: rgba(26, 25, 25, 1);
        padding: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: .3s;

        &:hover {
            opacity: 0.7;
        }

        @include vp-767 {
            max-width: 100%;
        }

    }

    &__block-text {
        font-family: Gotham Pro;
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        letter-spacing: -1.1%;
        color: rgba(255, 255, 255, 1);
        margin: 0;
        background: rgba(255, 255, 255, 0.15);

        span {
            font-weight: 500;
        }
    }

    &__block:first-child {
        max-width: 460px;

        .intro__block-text {
            width: 100%;
            padding: 15px 63px 15px 15px;

            @include vp-767 {
                padding: 15px 85px 15px 15px;
            }
        }

        @include vp-767 {
            max-width: 100%;
            
            .two {
                display: none;
            }
        }
    }

    &__block:nth-child(2) {
        max-width: 290px;

        .intro__block-text {
            width: 100%;
            padding: 15px 45px 15px 15px;
        }

        @include vp-767 {
            max-width: 100%;
            .two {
                display: none;
            }
        }
    }
}